.progress-bar{width:100%;text-align:center;height:4px}.progress-bar .progress{position:relative;color:#ffffff;width:100%;background:#7c99d1;border-radius:none;text-align:center;z-index:10000}.progress-bar .progress .progress-percent{height:4px;position:absolute;background:-webkit-gradient(linear, right top, left top, color-stop(43.94%, #FD7243), to(#FDB343));background:-o-linear-gradient(right, #FD7243 43.94%, #FDB343 100%);background:linear-gradient(270deg, #FD7243 43.94%, #FDB343 100%);left:0;top:0;bottom:0;z-index:10001}
