.textLayer{position:absolute;left:0;top:0;right:0;bottom:0;overflow:hidden;opacity:0.2;line-height:1.0}.textLayer>div{color:transparent;position:absolute;white-space:pre;cursor:text;-webkit-transform-origin:0% 0%;-ms-transform-origin:0% 0%;transform-origin:0% 0%}.textLayer .highlight{margin:-1px;padding:1px;background-color:#2078A9}.textLayer .highlight.selected{background-color:#0094FF}.textLayer .highlight.begin{border-radius:4px 0px 0px 4px}.textLayer .highlight.end{border-radius:0px 4px 4px 0px}.textLayer .highlight.middle{border-radius:0px}.textLayer ::-moz-selection{background:blue}.textLayer ::selection{background:blue}.textLayer ::-moz-selection{background:blue}.textLayer .endOfContent{display:block;position:absolute;left:0px;top:100%;right:0px;bottom:0px;z-index:-1;cursor:default;-webkit-user-select:none;-ms-user-select:none;-moz-user-select:none}.textLayer .endOfContent.active{top:0px}.pdfViewer{height:100%;padding-top:22px;margin-left:190px;-webkit-transition:all 0.5s;-o-transition:all 0.5s;transition:all 0.5s;background-color:#F1F3F7}.pdfViewer.full{margin-left:0}.pdfViewer .canvasWrapper{overflow:hidden}.pdfViewer .page{direction:ltr;width:816px;height:1056px;margin:5px auto 5px auto;position:relative;overflow:visible;background-clip:content-box;background-color:white}.pdfViewer.removePageBorders .page{margin:0px auto 10px auto;border:none}.pdfViewer.singlePageView{display:inline-block}.pdfViewer.singlePageView .page{margin:0;border:none}.pdfViewer .page canvas{margin:0;display:block}
